<template>
	<scroll class="wrapper"  :data="singerlist"  data2="bottom" :style="{bottom:bottom}" >
			<div class="content">
				<van-list finished-text="">
					<a  v-for="(item,index) in singerlist" :key="index" @click.prevent="gotosheet(0,item.id)">
					<van-cell  >
						<template class="singertemplate"  slot="title">
							<div class="singerIMGDiv"  v-lazy:background-image="item.picUrl">
							</div> 
							<p class="singerP" v-html="item.name"></p>
						</template>
					</van-cell>
					<div class="van-hairline--bottom"></div>
					</a>
				</van-list>
			</div>
	</scroll>
</template>

<script>
import configs from "./../config/appConfig.js";
import scroll from "./scroll.vue"
import { mapState,mapGetters , mapMutations} from "vuex"
export default {
	data() {
		return {
			singerlist:[],
		};
	},
	computed: {
		...mapState(["showPlayer","bottom"]),
	},
	created() {
		var _this=this;
			this.$http.get(configs.APIURL+"/top/artists")
			.then(response=>{
				_this.singerlist=response.data.artists;
			}).catch(err=>{

			});
	},
	methods: {
		gotosheet(sheetid,singerid){
			if(!singerid){
				singerid=0;
			}
			this.$router.push({ name: 'songsheet', params: { sheetid,singerid }})
		},
	},
	components: {
		scroll
	},
};
</script>

<style scoped>
	.wrapper{
		position: fixed;
		top: 90px;
		bottom: 0px;
		right: 0px;
		left: 0px;
		overflow: hidden;
	}
	.singerIMGDiv{
		height: 50px;
		width: 50px;
		border-radius:50%;
		background-position:center center;
		background-size:auto 100%;
		float: left;
	}
	.singerP{
		float: left;
		margin-left: 20px;
	}
</style>
